import Peer from 'peerjs';

const PeerKey = 'lwjd5qra8257b9';

class VideoCallService {

    peerInstance = null;

    constructor() {
        if (!this.peerInstance) {
            this.peerInstance = new Peer({ key: PeerKey })
        }
    }

    get PeerInstance() {
        if (!this.peerInstance) {
            this.peerInstance = new Peer({ key: PeerKey })
        }

        return this.peerInstance;
    }

    openStream = () => {
        const config = { audio: false, video: true };
        return navigator.mediaDevices.getUserMedia(config);
    }

    playVideoStream = (videlHtmlElement, stream) => {
        videlHtmlElement.srcObject = stream;
        videlHtmlElement.play();
    }

    //Open a connection
    openConnect = (callbackFunc) => {
        this.peerInstance.on('open', callbackFunc);
    }

    //Receiving a call
    ReceiveCall = (myVideoHtmlElement, theirVideoHtmlElement) => {
        this.peerInstance.on('call', call => {
            this.openStream().then(stream => {
                call.answer(stream);

                this.playVideoStream(myVideoHtmlElement, stream);

                call.on('stream', remoteStream => this.playVideoStream(theirVideoHtmlElement, remoteStream));
            })
        })
    }

    //Calling
    Call = (remoteId, myVideoHtmlElement, theirVideoHtmlElement) => {
        this.openStream().then(stream => {
            this.playVideoStream(myVideoHtmlElement, stream);

            const call = this.peerInstance.call(remoteId, stream);

            call.on('stream', remoteStream => this.playVideoStream(theirVideoHtmlElement, remoteStream));
        })
    }

}

export default new VideoCallService();