import React, { Component } from 'react';
import MessageBoard from './components/MessageBoard';
import MessageMaker from './components/MessageMaker';

import Login from '../src/screens/Login';

import TextMessageService from './services/TextChatService';
import VideoCallService from './services/VideoCallService';


class App extends Component {

  constructor(props) {
    super(props);

    this.state = {
      messages: [

      ],
      isLoggedIn: false,
      username: ''
    }
  }

  componentDidMount() {
    TextMessageService.receive(this.messageReceived);

    //
    VideoCallService.openConnect(function(myRemoteId){
      console.log(myRemoteId);
    });
  }

  messageReceived = (message) => {
    this.onShowMessage(message)
  }

  onSentMessage = (message) => {
    this.onShowMessage(message);
  }

  onLoginResponse = (response) => {
    if(response.code === 1){
      this.setState({
        isLoggedIn: true,
        username: response.username
      });
    }else{
      alert(response.message);
    }
  }

  onShowMessage = (message) => {
    let messages = this.state.messages;

    if(message.username){
      messages = messages.splice(messages.length, 0, {
        content: message.message,
        friend: message.username != this.state.username,
        username: message.username
      });
    }else{
      messages = messages.splice(messages.length, 0, {
        content: message,
        friend: false,
        username: this.state.username
      });
    }
    
    this.setState({ message: messages });
  }


  render() {
    if (this.state.isLoggedIn) {
      return (
        <div className="App">
          <header className="App-header"></header>

          <MessageBoard messages={this.state.messages} />

          <MessageMaker onSentMessage={this.onSentMessage} />
        </div>
      )
    } else {
      return (
        <div className="App" style={{ backgroundColor: 'black' }}>
          <Login onLoginResponse={this.onLoginResponse} />
        </div>
      )
    }
  }
}

export default App;
